.expense-chart {
  margin: 10px auto 10px auto;
  width: 90%;
  max-width: 800px;
  background-color: rgb(25, 115, 179);
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 30px;
  border-radius: 10px;
}

.chartbar {
  width: 20px;
  height: 150px;
  background-color: #fff;
  border-radius: 30px;
  overflow: hidden;
  display: flex;
  place-items: flex-end;
}

.chartbar-fill {
  width: 100%;
  background-color: rgb(250, 169, 93);
}

@media (max-width: 360px) {
  .expense-chart {
    display: none;
  }
}
